body,
html,
#app {
  color: #323232;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-size: cover;
  background-color: #030408;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
:focus {
  outline: none;
}
a {
  text-decoration: none;
  outline: none;
  color: #fff;
}
.pull-left {
  float: left !important;
}
.pull-right {
  float: right !important;
}
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 50px;
  border: none;
  padding-right: 15px;
  padding-left: 15px;
  background-color: #293c55;
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear;
  background-color: rgba(41, 60, 85, 0.4);
}
.header .navbar-header a {
  float: left;
  color: #777;
  height: 50px;
  padding: 15px 15px;
  font-size: 18px;
  line-height: 20px;
}
.header .navbar-header .navbar-logo {
  display: block;
  height: 32px;
  margin-top: -6px;
  margin-left: -2px;
}
.navbar-nav {
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear;
}
.navbar-nav li {
  position: relative;
  float: left;
}
.navbar-nav li a {
  color: #fff;
  background-color: transparent;
  font-size: 14px;
  position: relative;
  line-height: 20px;
  display: block;
  padding: 15px 20px;
  -webkit-transition: 0.5s background-color;
  transition: 0.5s background-color;
}
.navbar-nav li a:before {
  content: '';
  position: absolute;
  left: 50%;
  right: 50%;
  top: 0;
  background: #42B983;
  height: 4px;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}
.navbar-nav li a:hover {
  background-color: #162436;
}
.navbar-nav li a:hover:before {
  left: 0;
  right: 0;
}
.navbar-nav li a:focus:before {
  left: 0;
  right: 0;
}
.navbar-nav .active > a {
  padding-top: 11px;
  color: #fff;
  background-color: transparent;
  border-top: 4px solid #42B983;
  -webkit-transition: 0.5s background-color;
  transition: 0.5s background-color;
}
.navbar-nav .active > a:hover {
  color: #f9f9f9;
  background-color: #162436;
}
.navbar-left {
  float: left;
}
.navbar-right {
  float: right;
  margin-right: -15px;
  -webkit-transition: background-color 0.5s linear;
  transition: background-color 0.5s linear;
}

body,
html,
#app {
  color: #323232;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-size: cover;
  background-color: #030408;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style: none;
}
.clearfix {
  *zoom: 1;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
:focus {
  outline: none;
}
a {
  text-decoration: none;
  outline: none;
  color: #fff;
}
.pull-left {
  float: left !important;
}
.pull-right {
  float: right !important;
}
.main {
  margin: 50px auto;
  padding: 0 15px;
  text-align: left;
}
.charts-list {
  margin: 0 auto;
}
.chart {
  padding: 10px 10px 0;
  height: 278px;
  width: 282px;
  border: 1px solid #e5e5e5;
  display: inline-block;
  margin: 36px 5px;
  cursor: inherit;
  border-radius: 2px;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.chart .chart_wrap {
  height: 262px;
  width: 262px;
  margin: auto;
}
.chart .chart_bg {
  width: 262px;
  height: 206px;
  background: center no-repeat;
  background-size: cover;
  display: block;
}
.chart .chart_hover {
  color: #fff;
  text-align: center;
  height: 100%;
}
.chart .chart_magnifier {
  padding-top: 70px;
}
.chart .chart_info {
  height: 56px;
  padding: 0 3px;
}
.chart .chart_name,
.chart .chart_all {
  font-size: 14px;
  color: #767676;
  line-height: 30px;
  text-align: left;
}
.chart .chart_all {
  text-align: center;
  padding-top: 28px;
}
.chart .chart_detail {
  font-size: 12px;
  color: #999;
}
.chart .chart_icon {
  display: inline-block;
  height: 16px;
  width: 16px;
  background: url(data:image/png;base64,) no-repeat;
  background-position: 0 0;
  vertical-align: middle;
  margin-right: 2px;
}
.chart .chart_author_icon {
  background-position: 0 0;
}
.chart .chart_time_icon {
  background-position: -16px 0;
}
.chart .chart_icontxt {
  line-height: 16px;
  vertical-align: middle;
}
.chart .chart_magnifier_left {
  opacity: 0;
  width: 50%;
  height: 100%;
  float: left;
  text-align: center;
  line-height: 206px;
}
.chart .chart_magnifier_left div:nth-child(1) {
  height: 60%;
}
.chart .chart_magnifier_left div:nth-child(2) {
  height: 28px;
  line-height: 28px;
}
.chart .chart_magnifier_left:hover {
  opacity: 1;
  cursor: pointer;
}
.chart .chart_magnifier_left:hover .chart_hover {
  background: rgba(0, 0, 0, 0.3);
}
.chart .chart_magnifier_right {
  opacity: 0;
  width: 50%;
  height: 100%;
  float: right;
  text-align: center;
  line-height: 206px;
}
.chart .chart_magnifier_right div:nth-child(1) {
  height: 60%;
}
.chart .chart_magnifier_right div:nth-child(2) {
  height: 28px;
  line-height: 28px;
}
.chart .chart_magnifier_right:hover {
  opacity: 1;
  cursor: pointer;
}
.chart .chart_magnifier_right:hover .chart_hover {
  background: rgba(0, 0, 0, 0.3);
}
.chart:hover {
  -webkit-box-shadow: 0 0 16px rgba(0, 0, 0, 0.45);
          box-shadow: 0 0 16px rgba(0, 0, 0, 0.45);
}
@media (min-width: 992px) {
  .charts-list {
    width: 921px;
  }
}
@media (min-width: 1280px) {
  .charts-list {
    width: 1230px;
  }
}
@media (min-width: 1600px) {
  .charts-list {
    width: 1537px;
  }
}
@media only screen and (max-width: 767.99px) {
  #charts-list-ul {
    text-align: center;
  }
}

